.background {
    position: fixed;
    z-index: -9999;
    user-select: none;
    display: none;
}
.background svg {
    color: var(--BG);
    position: absolute;
    width: 100%;
    height: 100%;
    user-select: none;
    transition: 5s;
}
.background svg path {
    transition: 5s;
}
@media screen and (min-width: 480px) {
    .background {
        display: block;
        width: 230%; height: 230%;
        top: -65%;
        right: -125%;
    }
    .background.bg-about {
        top: -60%;
        right: -130%;
    }
    .background.bg-projects {
        top: -60%;
        right: -140%;
    }
    .background.bg-skills {
        top: -50%;
        right: -140;
    }
    .background.bg-tools {
        top: -60%;
        right: -140%;
    }
}
@media screen and (min-width: 600px) {
    .background {
        width: 210%; height: 210%;
        top: -60%;
        right: -110%;
    }
    .background.bg-about {
        top: -50%;
        right: -110%;
    }
    .background.bg-projects {
        top: -50%;
        right: -120%;
    }
    .background.bg-skills {
        top: -40%;
        right: -115%;
    }
    .background.bg-tools {
        top: -50%;
        right: -120%;
    }
}
@media screen and (min-width: 768px) {
    .background {
        width: 210%; height: 210%;
        top: -50%;
        right: -100%;
    }
    .background.bg-about {
        top: -40%;
        right: -105%;
    }
    .background.bg-projects {
        top: -45%;
        right: -120%;
    }
    .background.bg-skills {
        top: -30%;
        right: -105%;
    }
    .background.bg-tools {
        top: -50%;
        right: -125%;
    }
}
@media screen and (min-width: 800px) {
    .background {
        width: 215%; height: 215%;
        top: -60%;
        right: -110%;
    }
    .background.bg-about {
        top: -55%;
        right: -105%;
    }
    .background.bg-projects {
        top: -45%;
        right: -120%;
    }
    .background.bg-skills {
        top: -30%;
        right: -105%;
    }
}
