nav {
	position: fixed;
	bottom: 0;
	width: 100%;
	z-index: 9999;
}
nav svg {
	transition: 5s;
}
.li-grid2 {
	display: none;
}
.li-hr {
	display: none;
}
.theme {
	position: fixed;
	top: 1em;
	right: 1em;
}
.li-grid1 {
	background: var(--transparent);
	display: flex;
	justify-content: space-around;
	padding: 0.5em 0;
	transition: 5s;
}
nav svg {
	font-size: 1.875em;
	color: var(--white);
}

/* MEDIA QUERIES */
/* 375 - iPhoneX */
@media screen and (min-width: 375px) and (min-height: 810px) {
	.li-grid1 {
		padding: 0.625em 0;
	}
	nav svg {
		font-size: 2.125em;
	}
}
/* 411 */
@media screen and (min-width: 410px) {
	nav svg {
		font-size: 2.125em;
	}
}

/* Tablets - 480 */
@media screen and (min-width: 480px) and (min-height: 850px) {
	nav svg {
		font-size: 2.5em;
	}
}
/* 600 */
@media screen and (min-width: 600px) and (min-height: 960px) {
	nav svg {
		font-size: 2.75em;
	}
}
/* 768 */
@media screen and (min-width: 768px) and (min-height: 1024px) {
	nav svg {
		font-size: 3em;
	}
	.li-grid1 {
		justify-content: space-evenly;
	}
}
/* 800 */
@media screen and (min-width: 800px) and (min-height: 1280px) {
	nav svg {
		font-size: 3.25em;
	}
}
/* Desktops */
/* 962 - hybrid */
@media screen and (min-width: 960px) {
	nav {
		position: fixed;
		bottom: 3.75%;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 7.5%;
	}
	nav ul {
		display: flex;
		grid-template-columns: repeat(5, auto);
		width: 100%;
		justify-content: center;
		align-items: center;
	}
	.li-grid {
		display: grid;
		justify-content: center;
		align-items: flex-end;
	}
	.li-grid1 {
		background: none;
		padding: 0;
	}
	.li-grid1 {
		grid-template-columns: repeat(5, 1fr);
	}
	.li-grid2 {
		grid-template-columns: repeat(3, 1fr);
		justify-content: flex-start;
		align-items: flex-end;
	}
	.li-grid1 div:not(:first-child) {
		margin-left: 2.5em;
	}
	.li-grid2 a:not(:first-child) {
		margin-left: 2em;
	}
	.li-grid div,
	.li-grid a {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	nav svg {
		font-size: 1.75em;
		transition: 0.25s;
	}
	nav svg:hover {
		color: var(--icons-transparent);
	}
	.li-grid2 svg {
		font-size: 1.625em;
	}
	.li-grid2 a:last-child svg {
		font-size: 1.75em;
	}
	.li-hr {
		width: 100%;
		height: 100%;
		display: grid;
		grid-template-columns: 1fr;
		justify-content: center;
		align-items: center;
		margin: 0 2em;
	}
	.li-hr hr {
		width: 100%;
		height: 2px;
		border: none;
		border-bottom: 2px solid var(--white);
		transition: 0.25s;
	}
	.li-hr1 {
		width: 32.5%;
	}
	nav ul li:last-child svg {
		font-size: 2em;
		cursor: pointer;
	}
	.theme {
		display: block;
		position: static;
	}
}
/* 1280 */
@media screen and (min-width: 1280px) {
	nav {
		bottom: 5%;
		padding: 0 10%;
	}
}
@media screen and (min-width: 1280px) and (min-height: 880px) {
	nav svg {
		font-size: 2em;
	}
	nav ul li:last-child svg {
		font-size: 2.375em;
	}
}
/* 1360 */
@media screen and (min-width: 1360px) {
	nav {
		bottom: 3.75%;
		padding: 0 10%;
	}
	nav svg {
		font-size: 2em;
	}
	.li-grid2 svg {
		font-size: 1.75em;
	}
	.li-grid2 a:last-child svg {
		font-size: 1.875em;
	}
	.li-grid1 div:not(:first-child) {
		margin-left: 2.875em;
	}
	.li-grid2 a:not(:first-child) {
		margin-left: 2.325em;
	}
	.li-hr {
		margin: 0 2.25em;
	}
}
/* 1400 */
@media screen and (min-width: 1400px) {
	nav {
		bottom: 5%;
	}
	nav svg {
		font-size: 2.25em;
	}
	.li-grid2 svg {
		font-size: 2em;
	}
	.li-grid2 a:last-child svg {
		font-size: 2.125em;
	}
	nav ul li:last-child svg {
		font-size: 2.5em;
	}
}
/* 1440 */
@media screen and (min-width: 1440px) {
	nav {
		bottom: 3.75%;
	}
	nav svg {
		font-size: 2.125em;
	}
	.li-grid2 svg {
		font-size: 1.875em;
	}
	.li-grid2 a:last-child svg {
		font-size: 2em;
	}
	nav ul li:last-child svg {
		font-size: 2.375em;
	}
}
/* 1536 */
@media screen and (min-width: 1536px) {
	nav svg {
		font-size: 2.125em;
	}
	.li-grid2 svg {
		font-size: 1.875em;
	}
	.li-grid2 a:last-child svg {
		font-size: 2em;
	}
	nav ul li:last-child svg {
		font-size: 2.5em;
	}
}
/* 1680 */
@media screen and (min-width: 1680px) {
	nav svg {
		font-size: 2.325em;
	}
	.li-grid2 svg {
		font-size: 2em;
	}
	.li-grid2 a:last-child svg {
		font-size: 2.125em;
	}
	nav ul li:last-child svg {
		font-size: 2.675em;
	}
	.li-grid1 div:not(:first-child) {
		margin-left: 3.25em;
	}
	.li-grid2 a:not(:first-child) {
		margin-left: 2.5em;
	}
}
/* 1920 */
@media screen and (min-width: 1920px) {
	nav {
		padding: 0 9.5%;
	}
	nav svg {
		font-size: 2.25em;
	}
	.li-grid2 svg {
		font-size: 2em;
	}
	.li-grid2 a:last-child svg {
		font-size: 2.25em;
	}
	.li-grid1 div:not(:first-child) {
		margin-left: 3.125em;
	}
	.li-hr {
		margin: 0 2.5em;
	}
}

