@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap');
* {
	box-sizing: border-box;
	list-style: none;
	text-decoration: none;
	margin: 0;
	padding: 0;
	font-family: 'Rubik', sans-serif;
	font-weight: 400;
}
.powered {
	position: absolute;
	top: 0.5em;
	left: 0.5em;
	justify-content: center;
	align-items: center;
	color: var(--white);
	user-select: none;
	display: none;
	font-size: 1.125em;
	font-weight: 300;
	transition: 5s;
}
.powered svg {
	font-size: 1.5em;
}
#App {
	width: 100vw;
	height: 100vh;
	color: var(--white);
	transition: 5s;
}
body {
	background: var(--greyish);
	transition: 5s;
}
body.preload {
	-webkit-transition: none !important;
	-moz-transition: none !important;
	-ms-transition: none !important;
	-o-transition: none !important;
	transition: none !important;
}

/* powered */
@media screen and (min-width: 600px) {
	.powered {
		display: flex;
		font-size: 1.25em;
	}
	.powered svg {
		font-size: 1.5em;
	}
}
@media screen and (min-width: 768px) {
	.powered {
		font-size: 1.375em;
	}
}
@media screen and (min-width: 800px) {
	.powered {
		font-size: 1.5em;
	}
	.powered svg {
		font-size: 1.675em;
	}
}
@media screen and (min-width: 960px) {
	body {
		position: fixed;
		overflow: hidden;
	}
	#App {
		position: fixed;
		overflow: hidden;
	}
	.powered {
		font-size: 1em;
	}
	.powered svg {
		font-size: 1.5em;
	}
}
@media screen and (min-width: 1024px) {
	.powered {
		font-size: 1em;
	}
}
@media screen and (min-width: 1400px) {
	.powered {
		font-size: 1.125em;
	}
}
@media screen and (min-width: 1680px) {
	.powered {
		font-size: 1.175em;
	}
}
