:root {
    --white: white;
    --greyish: #1d1c1b;
    --transparent: #111;
    --icons-transparent: rgba(255, 255, 255, 0.25);
    --div-transparent: rgba(0, 0, 0, .625);
    --BG: rgba(0, 0, 0, 0.325);
  }
  /* theme */
  .light {
    --white: #1d1c1b;
    --greyish: #f0f0f0;
    --transparent: #ccc;
    --icons-transparent: rgba(0, 0, 0, 0.375);
    --BG: rgba(0, 0, 0, 0.25);
}

