header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
header div {
    display: flex;
    flex-direction: column;
}
header img {
    width: 55%;
    margin: 2.5em 0 1em;
}
header h1 {
    font-size: 2.625em;
}
.h1-desktop {
    display: none;
}
header h1 span {
    font-weight: 500;
}
header h2 {
    font-size: 1.25em;
    font-weight: 300;
    position: relative;
    margin: .5em 0 1.75em;
}
header h2::before {
    content: '.';
    opacity: 0;
    margin-left: -4px;
}
header h2::after {
    content: '';
    position: absolute;
    margin-left: 2px;
    width: 1px;
    height: 100%;
    border-right: 2px solid var(--white);
    transition: 5s;
}
@keyframes type {
    0%,50% { opacity: 1; }
    51%, 100% { opacity: 0; }
}
.h2::after {
    animation: type .875s 2 linear;
}
.header-social {
    width: 50%;
    display: grid;
    grid-template-columns: repeat(3, auto);
    justify-content: space-between;
}
.header-social svg {
    font-size: 1.75em;
    color: var(--white);
}

/* MEDIA QUERIES */
/* mobile */
@media screen and (min-width: 360px) {
    header img { width: 50%; }
    .header-social svg { font-size: 1.875em; }
}
@media screen and (min-width: 375px) and (min-height: 812px) {
    header img { margin: 5em 0 2em; }
    header h2 { margin: .5em 0 2.5em; }
}
@media screen and (min-width: 384px) {
    header h1 { font-size: 2.875em; }
}
/* tablets */
@media screen and (min-width: 480px) {
    header h1 { font-size: 3.5em; }
    header img { margin: 3em 0 2em; }
    header h2 { font-size: 1.5em; }
    .header-social svg { font-size: 2.25em; }
}
@media screen and (min-width: 600px) {
    header img {
        width: 42.5%;
        margin: 5em 0 1.75em;
    }
    header h1 { font-size: 3.75em; }
    header h2 { font-size: 1.675em; }
    .header-social svg { font-size: 2.325em; }
}
@media screen and (min-width: 768px) {
    header img {
        width: 35%;
        margin: 5em 0 1.75em;
    }
    header h1 { font-size: 4em; }
    header h2 { font-size: 1.75em; }
    .header-social {
        width: 42.5%;
    }
    .header-social svg { font-size: 2.5em; }
}
@media screen and (min-width: 800px) {
    header img {
        width: 37.5%;
        margin: 7.5em 0 1.75em;
    }
    header h1 { font-size: 4.5em; }
    header h2 { font-size: 2em; }
    .header-social svg { font-size: 3em; }
}
/* desktops */
@media screen and (min-width: 960px) {
    .header-social { display: none; }    
    .h1-mobile { display: none; }
    .h1-desktop { display: inline-block; }
    header {
        user-select: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-80%, -80%);
        display: grid;
        grid-template-columns: repeat(2, auto);
        /* background: tomato; */
    }
    header div {
        display: flex;
        flex-direction: column;
        justify-content: center;
        order: -1;
        /* background: green; */
    }
    header h1 {
        font-size: 2.75em;
        white-space: nowrap;
        margin-bottom: .5em;
    }
    header h2 {
        font-size: 1.675em;
        margin: 0;
    }
    header img {
        margin: 0;
        margin-left: 25%;
        width: 137.5%;
    }
}
@media screen and (min-width: 1024px) {
    header img { width: 112.5%; }
}
@media screen and (min-width: 1280px) {
    header img { width: 67.5%; }
}
@media screen and (min-width: 1280px) and (min-height: 800px) {
    header h1 { font-size: 3.25em; }
    header h2 { font-size: 1.75em; }
    header img { width: 90%; }
}
@media screen and (min-width: 1360px) {
    header h1 { font-size: 2.75em; }
    header h2 { font-size: 1.375em; }
    header img { width: 60%; }
}
@media screen and (min-width: 1360px) {
    header h1 { font-size: 3em; }
    header h2 { font-size: 1.675em; }
    header img { width: 67.5%; }
}
@media screen and (min-width: 1400px) {
    header h1 { font-size: 3.375em; }
    header h2 { font-size: 1.75em; }
    header img { width: 77.5%; }
}
@media screen and (min-width: 1440px) {
    header h1 { font-size: 3.125em; }
    header h2 { font-size: 1.625em; }
    header img { width: 70%; }
}
@media screen and (min-width: 1536px) {
    header img { width: 60%; }
}
@media screen and (min-width: 1680px) {
    header h1 { font-size: 3.5em; }
    header h2 { font-size: 1.875em; }
    header img {
        width: 57.5%;
        margin-left: 20%;
    }
}
@media screen and (min-width: 1920px) {
    header h1 { font-size: 3.75em; }
    header h2 { font-size: 2em; }
    header img {
        width: 52.5%;
        margin-left: 15%;
    }
}