.projects {
	position: relative;
}
.projects .display-ul {
	display: grid;
	overflow-y: scroll;
	margin-top: 2.5em;
	gap: 1.75em;
  height: 75vh;
  margin-bottom: -2em;
}
.projects .display-ul li:last-child {
  padding-bottom: 10%;
}
.projects .display-ul img {
	width: 62.5%;
}
.projects ul h3 {
	color: var(--white);
	transition: 5s;
	font-size: 1em;
	margin-top: 0.25em;
}
.project-div {
	position: fixed;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	background: var(--div-transparent);
	z-index: 99999;
	display: none;
	/* flex */
	justify-content: center;
	align-items: center;
	transition: opacity 1s ease-in-out;
}
.show {
	display: flex;
}
.project-div menu {
	width: 90%;
	position: relative;
	background: var(--greyish);
}
.project-div figure {
	position: absolute;
	top: -8.75%;
	right: 0;
	width: 6.25%;
}
.project-div figure path {
	fill: white;
}
.project-div menu img {
	width: 100%;
}
.project-div .content {
	padding: 0 5% 5%;
}
.project-div h2 {
	font-weight: 500;
}
.project-div h4 {
	font-weight: 500;
	margin: 10% 0 3.75%;
}
.project-div p {
	text-align: left;
	font-size: 0.875em;
  margin: 0;
}
.div-ul {
  margin: 1em 0;
  display: flex;
  /* grid-template-columns: repeat(4, auto); */
  gap: .5em 1em;
  flex-wrap: wrap;
}
.div-ul li {
  display: flex;
  border: 1px solid var(--icons-transparent);
  padding: .25em .5em;
  display: block;
  width: fit-content;
  font-size: .875em;
}
.btns {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 0 5%;
  margin-top: 7.5%;
  gap: 25%;
}
.btns button {
  border: none;
  background: var(--white);
  display: inline-block;
  padding: .125em 0;
  border: 2px solid var(--white);
  border-radius: .175em;
}
.btns a {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btns span {
  display: block;
  font-weight: 500;
  font-size: 1em;
  color: var(--greyish);
  margin-left: .5em;
}
.btns svg {
  width: 25%;
}
.btns svg path {
  fill: var(--greyish)
}
.imgD{
  display: none;
}
.true {
  color: green;
}
.false {
  color: red;
}

/* MEDIA QUERIES */
@media screen and (min-width: 375px) {
  .project-div menu {
    width: 85%;
  }
}
@media screen and (min-width: 384px) {
  .project-div menu {
    width: 80%;
  }
}
@media screen and (min-width: 410px) {
  .projects ul h3 {
    font-size: 1.125em;
  }
}
@media screen and (min-width: 480px) {
  .projects .display-ul {
    margin-top: 4em;
    gap: 2.5em;
  }
  .project-div menu {
    width: 75%;
  }
  .projects .display-ul img {
    width: 50%;
  }
}
@media screen and (min-width: 600px) {
  .project-div h4 {
    font-size: 1.375em;
  }
  .project-div p {
    font-size: 1.125em;
  }
  .div-ul li {
    padding: .25em .625em;
    font-size: 1.125em;
  }
}
@media screen and (min-width: 960px) {
  .imgD{
    display: block;
  }
  .imgM{
    display: none;
  }
}

@media screen and (min-width: 768px) and (min-height: 1024px) {
  .project-div menu {
    width: 70%;
  }
  .btns span {
    font-size: 1.25em;
  }
}
@media screen and (min-width: 800px) and (min-height: 1280px) {
  .btns span {
    font-size: 1.5em;
  }
  .project-div h4 {
    font-size: 1.75em;
  }
  .project-div p {
    font-size: 1.325em;
  }
  .div-ul li {
    font-size: 1.5em;
  }
}

@media screen and (min-width: 960px) {
  
  .projects .display-ul {
    grid-template-columns: repeat(4, 1fr);
    margin: 0;
    height: 100%;
    /* gap: 1.75em; */
    overflow: visible;
  }
  .projects .display-ul li:last-child {
    padding-bottom: 0;
  }
  .projects .display-ul img {
    cursor: pointer;
    width: 85%;
  }
  .projects ul h3 {
    font-size: 1em;
    margin-top: 0.375em;
  }
  .project-div menu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 62.5%;
  }
  .project-div figure { /* X */
    top: -12.5%;
    width: 3%;
    cursor: pointer;
  }
  .project-div figure path {
    transition: .25s;
	}
  .project-div figure:hover path {
		fill: var(--icons-transparent);
	}
  .project-div .content {
    padding: 0 2.5%;
    display: grid;
    justify-content: center;
    align-items: center;
    align-content: center;
    grid-template-rows: repeat(4, auto);
    height: 100%;
  }
  .project-div menu img {
    min-height: 100%;
    width: 50%;
  }
  .project-div h2 {
    font-size: 1.25em;
  }
  .div-ul {
    margin: .75em 0 1.25em;
  }
  .div-ul li {
    font-size: .75em;
  }
  .project-div h4 {
    font-size: .875em;
    margin: 0;
  }
  .project-div p {
    font-size: .8em;
    margin: .5em 0 1.5em;
  }
  .btns {
    margin-top: 0;
  }
  .btns svg {
    width: 22.5%;
  }
  .btns button  {
    transition: .375s;
  }
  .btns button:hover {
    background: transparent;
  }
  .btns button span {
    transition: .375s;
  }
  .btns button:hover span {
    color: var(--white);
  }
  .btns path {
    transition: .375s;
  }
  .btns button:hover path {
    fill: var(--white);
  }
}
@media screen and (min-width: 1280px) {
  .project-div menu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
  }
}
@media screen and (min-width: 1360px) {
  .projects .display-ul {
    grid-template-columns: repeat(5, 1fr);
  }
  .projects .display-ul img {
    width: 90%;
  }
  .project-div h2 {
    font-size: 1.375em;
  }
  .div-ul {
    margin: 1em 0 1.5em;
  }
  .project-div h4 {
    font-size: 1em;
  }
  .project-div p {
    font-size: .825em;
    margin: .75em 0 2em;
  }
}
@media screen and (min-width: 1400px) {
  .projects .display-ul {
    grid-template-columns: repeat(4, 1fr);
  }
  .projects .display-ul img {
    width: 85%;
  }
  .projects ul h3 {
    font-size: 1.125em;
    margin-top: 0.5em;
  }
}
@media screen and (min-width: 1440px) {
  .projects .display-ul {
    grid-template-columns: repeat(5, 1fr);
  }
  .projects .display-ul img {
    width: 85%;
  }
  .projects ul h3 {
    font-size: 1em;
    margin-top: 0.375em;
  }
}
@media screen and (min-width: 1536px) {
  .project-div h2 {
    font-size: 1.625em;
  }
  .div-ul li {
    font-size: .825em;
  }
  .project-div p {
    font-size: .875em;
    margin: .75em 0 2.5em;
  }
}
@media screen and (min-width: 1680px) {
  .projects .display-ul img {
    width: 85%;
  }
  .projects ul h3 {
    font-size: 1.125em;
  }
  .project-div h2 {
    font-size: 1.875em;
  }
  .div-ul li {
    font-size: .875em;
  }
  .project-div h4 {
    font-size: 1.25em;
  }
  .project-div p {
    font-size: 1em;
    margin: .75em 0 2.375em;
  }
}
@media screen and (min-width: 1920px) {
  .project-div menu {
    width: 45%;
  }
  .div-ul {
    margin: 1.5em 0 2em;
  }
  .project-div h4 {
    font-size: 1.125em;
  }
  .project-div p {
    font-size: .875em;
    margin: .75em 0 2.375em;
  }
}