main {
    padding: 6.25% 10% 0;
}
main h1 {
    font-size: 1.875em;
    font-weight: 500;
    margin-bottom: 1em;
}
main h2 {
    font-weight: 300;
    font-size: 1.375em;
}
main .section-about h2 {
    font-weight: 400;
}
main p {
    margin-top: .25em;
}
main section {
    display: grid;
}
.section-skills article, .section-tools article {
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
}
.section-skills article:last-child, .section-tools article:last-child {
    margin: 0;
}
.section-skills svg, .section-tools svg {
    font-size: 2em;
    margin-right: .5em;
}
/* about */
.section-about {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    gap: 6.75%;
}
.section-about svg {
    display: none;
}
main p {
    text-align: justify;
    font-weight: 300;
    font-size: .95em;
}
.section-skills h1, .section-tools h1 {
    margin-bottom: .75em;
}
.section-skills div:last-child h1 {
    margin-top: .25em;
}
.section-skills h2, .section-tools h2 {
    font-size: 1.25em;
}
.section-skills svg, .section-tools svg {
    font-size: 1.875em;
    transition: .325s;
}
/* tools */
.section-tools article:not(:last-child) {
    margin-bottom: 1.5em;
}
.section-skills div:last-child {
    margin-top: 1.5em;
}
.section-tools .figma path {
    transition: .325s;
}

/* MEDIA QUERIES */
@media screen and (min-width: 360px) {
    main {
        padding-top: 10%;
    }
    main h1 {
        font-size: 2em;
        margin-bottom: 1.25em;
    }
    main h2 {
        font-size: 1.5em;
    }
    main p {
        font-size: 1em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 1.325em;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2em;
    }
}
@media screen and (min-width: 375px) {
    .section-skills h1, .section-tools h1 {
        margin-bottom: .875em;
    }
    .section-skills div:last-child h1 {
        margin-top: .5em;
    }
    .section-skills article, .section-tools article {
        margin-bottom: 1.375em;
    }
    .section-tools article:not(:last-child) {
        margin-bottom: 1.75em;
    }
}
@media screen and (min-width: 375px) and (min-height: 810px) {
    main {
        padding: 10% 10% 0;
    }
    .section-about {
        gap: 10%;
    }
    .section-skills div:last-child h1 {
        margin-top: 1em;
    }
    main h2 {
        font-size: 1.625em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 1.5em;
    }
    .section-skills article, .section-tools article {
        margin-bottom: 1.75em;
    }
    .section-tools article:not(:last-child) {
        margin-bottom: 2em;
    }
}
@media screen and (min-width: 410px) {
    main h1 {
        font-size: 2.25em;
        margin-bottom: 1.25em;
    }
    main h2 {
        font-size: 1.675em;
    }
    main p {
        font-size: 1em;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2.125em;
    }
}
/* tablets */
@media screen and (min-width: 480px) {
    main {
        padding: 12.5% 11.25% 0;
    }
    main h1 {
        font-size: 2.325em;
        margin-bottom: 1.125em;
    }
    main h2 {
        font-size: 1.75em;
    }
    main p {
        font-size: 1.125em;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2.25em;
    }
    .section-tools article:not(:last-child) {
        margin-bottom: 2.25em;
    }
}
@media screen and (min-width: 600px) {
    main {
        padding: 17.5% 11.25% 0;
    }
    main h1 {
        font-size: 2.5em;
    }
    main h2 {
        font-size: 2em;
    }
    main p {
        font-size: 1.25em;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2.625em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 1.75em;
    }
    .section-tools article:not(:last-child) {
        margin-bottom: 2.375em;
    }
}
@media screen and (min-width: 768px) {
    main h1 {
        font-size: 2.875em;
    }
    main h2 {
        font-size: 2.25em;
    }
    main p {
        font-size: 1.375em;
    }
    .section-about {
        gap: 12.5%;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2.75em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 1.875em;
    }
    .section-tools article:not(:last-child) {
        margin-bottom: 2.5em;
    }
}
@media screen and (min-width: 800px) {
    main h1 {
        font-size: 3.375em;
    }
    main h2 {
        font-size: 2.5em;
    }
    main p {
        font-size: 1.5em;
    }
    .section-about {
        gap: 12.5%;
    }
    .section-skills svg, .section-tools svg {
        font-size: 3.125em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 2.125em;
    }
    .section-skills article {
        margin-bottom: 2.75em;
    }
    .section-tools article:not(:last-child) {
        margin-bottom: 3em;
    }
}

@media screen and (min-width: 960px) {
    main {
        padding: 10% 5% 0;
    }
    main h1 {
        font-size: 2em;
        margin-bottom: 2.25em;
    }
    main h2 {
        font-size: 1.5em;
    }
    main p {
        font-size: .875em;
    }
    .section-about {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 1fr;
        gap: 5%;
    }
    .section-about svg {
        display: block;
        font-size: 2em;
        margin-bottom: .125em;
    }
    .section-skills {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .section-skills div:last-child, .section-skills div:last-child h1 {
        margin-top: 0;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 1.375em;
    }
    .section-skills h1, .section-tools h1 {
        margin-bottom: 2em;
    }
    .section-skills article {
        margin-bottom: 2em;
    }
    .section-tools {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 1fr);
        gap: 2em 0;
    }
    .section-tools article:not(:last-child) {
        margin: 0;
    }
    
}
@media screen and (min-width: 1024px) {
    main {
        cursor: default;
        user-select: none;
        z-index: 9999;
        padding: 10% 7.5% 0;
    }
}
@media screen and (min-width: 1280px) {
    main {
        padding: 7.5% 10% 0;
    }
    .section-skills h1, .section-tools h1 {
        margin-bottom: 1.5em;
    }
    main h1 {
        margin-bottom: 1.75em;
    }
    main h2 {
        font-size: 1.625em;
    }
    main p {
        font-size: 1em;
        margin-top: .625em;
    }
}
@media screen and (min-width: 1280px) and (min-height: 657px) {
