    }
    main h1 {
        font-size: 2.125em;
        margin-bottom: 1.75em;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2.125em;
    }
}
@media screen and (min-width: 1280px) and (min-height: 880px) {
    main {
        padding: 10% 10% 0;
    }
    main h1 {
        font-size: 2.5em;
        margin-bottom: 2.5em;
    }
    main h2 {
        font-size: 1.75em;
    }
    main p {
        font-size: 1.125em;
    }
    .section-about svg {
        font-size: 2.25em;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2.375em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 1.5em;
    }
}
@media screen and (min-width: 1360px) {
    main {
        padding: 10% 10% 0;
    }
}
@media screen and (min-width: 1400px) {
    .section-tools {
        gap: 2.5em 0;
    }
    .section-skills article {
        margin-bottom: 2.25em;
    }
    .section-skills h1 {
        margin-bottom: 1.75em;
    }
    .section-skills h2, .section-tools h2 {
