        font-size: 3em;
        margin-bottom: 2.25em;
    }
    main h2 {
        font-size: 2.125em;
    }
    main p {
        font-size: 1.375em;
    }
    .section-about svg {
        font-size: 2.5em;
    }
    .section-skills svg, .section-tools svg {
        font-size: 2.675em;
    }
    .section-skills h2, .section-tools h2 {
        font-size: 1.675em;
